<template>
    <div class="row">
        <span class="card__heading">
            <h2>Projects</h2>
        </span>
        <div class="work__boxes">

            <div class="work__box">
                <div class="work__text">
                    <h3 class="work__title">Customer Success Portal</h3>
                    <span class="work__description">
                        The CSP I've been developing for my company is essentially an application that sits in the
                        middle
                        of the end-user submitting support tickets, searching a knowledgebase and reviewing
                        performance/SLA reports, and the help desk software we consume the API endpoint from. We found
                        that a lot of the main players in the help desk world have a lacklustre user experience when it
                        comes to their portals, and miss a lot of the goalposts in customization. By building an in
                        house
                        solution, we can circumvent those issues and ultimately offer a better experience for our
                        clients.
                    </span>
                    <span class="work__description">
                        As the project is an internal one, unfortunately, I can't share the source code but have added a
                        link to a development version I use myself for testing.
                    </span>
                    <ul class="work__list">
                        <li>HTML5</li>
                        <li>SCSS</li>
                        <li>Vue JS</li>
                        <li>PHP</li>
                        <li>Laravel</li>
                    </ul>
                </div>

                <span class="dummy"></span>
                <div class="work__image-box">
                    <div class="work__image">
                        <transition-group name="fade" tag="div">
                            <div v-for="i in [currentIndex]" :key="i">
                                <img class="work__image--img" :src="currentImg" />
                            </div>
                        </transition-group>
                        <a class="prev" @click="prev" href="javascript:;">&#10094;</a>
                        <a class="next" @click="next" href="javascript:;">&#10095;</a>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import csp1 from '../../assets/images/gallery/csp/csp1.png'
    import csp2 from '../../assets/images/gallery/csp/csp2.png'
    import csp3 from '../../assets/images/gallery/csp/csp3.png'
    import csp4 from '../../assets/images/gallery/csp/csp4.png'
    import csp5 from '../../assets/images/gallery/csp/csp5.png'
    import csp6 from '../../assets/images/gallery/csp/csp6.png'
    import csp7 from '../../assets/images/gallery/csp/csp7.png'

    export default {
        data() {
            return {
                timer: null,
                currentIndex: 0,
                images: [
                    csp1,
                    csp2,
                    csp3,
                    csp4,
                    csp5,
                    csp6,
                    csp7
                ],
            }
        },
        methods: {
            startSlide() {
                this.timer = setInterval(this.next, 4000);
            },
            next() {
                this.currentIndex += 1;
            },
            prev() {
                this.currentIndex -= 1;
            },
        },
        computed: {
            currentImg() {
                return this.images[Math.abs(this.currentIndex) % this.images.length];
            }
        },
        mounted() {
            this.startSlide()
        }
    }
</script>