<template onscroll="onScroll">
  <div>
    <header class="header" role="banner" id="top">
      <div class="row">
        <div class="nav">
          <nav>
            <div class="nav__item" @click="jumpFocus('skillsBox')"><span class="nav__link">Skills</span></div>
            <div class="nav__item" @click="jumpFocus('projectsBox')"><span class="nav__link">Projects</span></div>
            <div class="nav__item" @click="jumpFocus('aboutBox')"><span class="nav__link">About</span></div>
            <div class="nav__item" @click="jumpFocus('blogBox')"><span class="nav__link">Blog</span></div>
            <div class="nav__item" @click="jumpFocus('contactBox')"><span class="nav__link">Contact</span></div>
          </nav>
        </div>
      </div>
      <div class="header__text-box row">
        <div class="header__text">
          <h1 class="heading-primary">
            <span>Leigh Latham</span>
          </h1>
          <p>A Fullstack Developer based in Shropshire, England.</p>
          <input @click="jumpFocus('contactBox')" type="button" class="btn btn--heading btn--pink btn-bold" value="Contact">
        </div>
      </div>
      <span class="design--top" ref="topBox"></span>
    </header>

    <main role="main">
      <div class="gradient">

        <section class="skills" id="skills" ref="skillsBox">
          <skills></skills>
        </section>

        <section class="work" id="projects" ref="projectsBox">
          <project-csp></project-csp>
        </section>

        <section class="about" id="me" ref="aboutBox">
          <about></about>
        </section>
      </div>
      <span class="design--bottom"></span>
    </main>

    <div class="design">
      <section class="contact" id="contact" ref="contactBox">
        <div class="row">
          <span class="card__heading">
            <h2>Contact Me</h2>
          </span>
          <div class="contact__text">
            <p>
              I design and implement user-friendly, performant, scalable websites and web applications
              suited to requirements. If you would like to get in touch for any reason, you may use the
              button available below.
            </p>
            <div class="contact__button">
              <a href="mailto:leighlatham123@gmail.com" class="btn btn--pink btn-bold">E-mail</a>
            </div>
          </div>
        </div>
      </section>

      <footer role="contentinfo" class="footer">
        <div class="row">

          <ul class="footer__social-links">
            <li class="footer__social--item">
              <a href="https://github.com/leighlatham123/" target="_blank">
                <font-awesome-icon class="footer__social--item-icon" :icon="['fab', 'git-square']" />
              </a>
            </li>
            <li class="footer__social--item">
              <a href="https://www.linkedin.com/in/leigh-latham/" target="_blank">
                <font-awesome-icon class="footer__social--item-icon" :icon="['fab', 'linkedin']" />
              </a>
            </li>
          </ul>
        </div>
        <div class="row">
          <span class="footer__text">Designed and implemented by Leigh Latham</span>
        </div>
      </footer>
    </div>
    <div class="skip__wrapper" ref="skipper" @click="scrollToTop()">
      <font-awesome-icon class="skip__icon" :icon="['fa', 'arrow-alt-circle-up']" />
    </div>
  </div>
</template>

<script>
import ProjectCsp from './projects/project_CSP.vue'
import Skills from './skills/SkillsSection.vue'
import About from './about/AboutSection.vue'

export default {
  name: 'MainPortfolio',
  props: {},
  components: {
    ProjectCsp,
    Skills,
    About
  },
  data() {
    return {
      offsetTop: 0
    }
  },
  methods: {
    jumpFocus(ref) {
      this.$refs[ref].scrollIntoView()
    },
    onScroll() {
      this.offsetTop = window.pageYOffset || document.documentElement.scrollTop
      this.scrollFunction()
    },
    isElementInViewport(el) {
      var rect = el.getBoundingClientRect();
      return (
        rect.top >= -300 &&
        rect.left >= 0 &&
        rect.bottom <= (window.innerHeight || document.documentElement.clientHeight) +400 &&
        rect.right <= (window.innerWidth || document.documentElement.clientWidth)
      );
    },
    scrollFunction() {
      if (this.offsetTop > 600) {
        this.$refs.skipper.style.opacity = 1;
      } else {
        this.$refs.skipper.style.opacity = 0;
      }
    },
    callbackFunc() {
      let elements = ['.header__text', '.skills', '.work', '.about'];
      
      elements.forEach(element => {
        let items = document.querySelectorAll(element);

        for (var i = 0; i < items.length; i++) {
          if (this.isElementInViewport(items[i])) {
            items[i].classList.add('slide-in');
            items[i].classList.remove('slide-out');
          }else {
            items[i].classList.add('slide-out');
            items[i].classList.remove('slide-in');
          }
        }
      });
    },
    scrollToTop() {
      window.scrollTo(0,0);
    }
  },
  watch: {
    offsetTop () {
        this.callbackFunc()
    }
  },
  computed: {
    
  },
  created () {
    window.addEventListener('scroll', this.onScroll);
  },
  destroyed () {
    window.removeEventListener('scroll', this.onScroll);
  }
}
</script>